<script lang="ts">/* for every keydown event in input the function inputItem() is called, adds content to input array if last key was "Enter" and input is non-empty, reactively updates everything */
/* clicking filter changes URL fragment, "hashchange" event handler calls applyFilter(), sets selectedFilter, reactively updates filteredItems */
import { uuid } from "./helpers";
import { slide } from "svelte/transition";
var Filter;
(function (Filter) {
    Filter["All"] = "";
    Filter["Active"] = "active";
    Filter["Completed"] = "completed";
})(Filter || (Filter = {}));
let items;
let selectedFilter = Filter.All;
// let selectedItemToEditIndex: number = undefined;
// load items from previous session if available
try {
    items = JSON.parse(localStorage.getItem('todos-svelte')) || [];
}
catch (err) {
    items = [];
}
$: filteredItems =
    selectedFilter === Filter.Active
        ? items.filter(item => !item.completed)
        : selectedFilter === Filter.Completed
            ? items.filter(item => item.completed)
            : items;
$: numCompleted = items.filter(item => item.completed).length;
$: numActive = items.length - numCompleted;
$: try {
    localStorage.setItem('todos-svelte', JSON.stringify(items));
}
catch (err) {
    // noop
}
function applyFilter() {
    const urlFragment = window.location.hash;
    if (urlFragment === `#/${Filter.Active}`) {
        selectedFilter = Filter.Active;
    }
    else if (urlFragment === `#/${Filter.Completed}`) {
        selectedFilter = Filter.Completed;
    }
    else {
        // is applied when URL fragment is `#/${Filter.All}`, or anything else including empty!
        selectedFilter = Filter.All;
    }
}
window.addEventListener("hashchange", applyFilter);
// execute at least once in case opens page directly with fragment URL
applyFilter();
function inputItem(event) {
    const key = event.key;
    const val = event.target.value.trim();
    if (key == "Enter" && val !== "") {
        addItem({
            id: uuid(),
            description: val,
            completed: false
        });
        event.target.value = "";
    }
}
function removeCompletedItems() {
    items = items.filter(item => !item.completed);
}
function removeItem(index) {
    items = [...items.slice(0, index), ...items.slice(index + 1)];
}
function addItem(item) {
    items = [...items, item];
}
</script>

<!-- TODO: toggle-all button, edit functionality -->

<h1>todos</h1>

<section class="todo-app">
    <section class="todo-header">
        <div class="empty"></div>
        <input
        class="todo-input"
        placeholder="What needs to be done?"
        on:keydown={inputItem}
        autofocus
        size="22"/>
    </section>

    {#if items.length > 0}
        <section class="todo-body">

            <ul class="todo-list">
                {#each filteredItems as item, index (item.id)}
                    <li transition:slide class="todo-item {item.completed ? 'completed' : ''}">
                        <input
                            class="completeToggle"
                            type="checkbox"
                            bind:checked={item.completed} />
                        <p class="description">{item.description}</p>
                        <button
                            class="removeItem"
                            on:click={() => removeItem(index)}>&times;</button>
                    </li>
                {/each}
            </ul>

            <section class="todo-footer">
                <button
                class="removeCompletedItems {numCompleted ? '' : 'visibilityCollapse'}"
                on:click={removeCompletedItems}>Clear completed</button>

                <ul class="filter">
                    <li>
                        <a
                            href={`#/${Filter.All}`}
                            class={selectedFilter == Filter.All ? 'checked' : ''}>All</a>
                    </li>
                    <li>
                        <a
                            href={`#/${Filter.Active}`}
                            class={selectedFilter == Filter.Active ? 'checked' : ''}>Active</a>
                    </li>
                    <li>
                        <a
                            href={`#/${Filter.Completed}`}
                            class={selectedFilter == Filter.Completed ? 'checked' : ''}>Completed</a>
                    </li>
                </ul>

                <p class="todo-count">{numActive} {numActive === 1 ? 'item' : 'items'} remaining</p>
            </section>
        </section>
    {/if}
</section>

